import React, { useEffect, useState } from "react";
import HeroSection from "../HeroSection";
import About from "../home-route/About";
import Services from "../home-route/Services";
import FAQ from "../home-route/FAQ";
import Pricing from "../home-route/Pricing";

import heroImg1 from "../../images/hero-image1.jpg";
import heroPhoneImg1 from "../../images/phone-hero-test.jpg";

function HomePage() {
  // useState used to see if menu is being displayed when on smaller screens
  const [phoneBg, setPhoneBg] = useState(false);

  const changePhoneBg = (state) => setPhoneBg(state);

  const toggleBg = () => {
    if (window.innerWidth <= 400) {
      changePhoneBg(true);
    } else {
      changePhoneBg(false);
    }
  };

  useEffect(() => {
    // Adds active to nav link so it's highlighted red
    const navHomeLink = document.querySelector(".nav-home-link");
    navHomeLink.classList.add("active");

    toggleBg();

    window.addEventListener("resize", toggleBg);

    return () => {
      // Removes active to nav link so it's not highlighted red
      navHomeLink.classList.remove("active");

      window.removeEventListener("resize", toggleBg);
    };
  }, []);

  return (
    <>
      <HeroSection
        fullPage={true}
        h1Text="Javier Mobile Car Wash"
        pText={<>Simi Valley CA and ALL surrounding areas</>}
        flip={true}
        backgroundImage={phoneBg ? `url(${heroPhoneImg1})` : `url(${heroImg1})`}
        color="white"
        bgGradientColor="black-bg"
      />
      <About />
      <Services />
      <Pricing />
      <FAQ />
    </>
  );
}

export default HomePage;
